import React from "react";
import styled from "styled-components";
import Button from "@mui/material/Button";
import { AlternateEmail, Key } from "@mui/icons-material";

import "./Login.css";

const RowContainer = styled.div`
  margin-bottom: 20px;
  color: white;
`;

const Container = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 50px;
  margin-bottom: 2px;
`;

const ImageForm = styled.div`
  width: 50px;
  height: 100%;
  display: flex;
  align-items: center;
  padding-right: 10px;
  justify-content: center;
  background: #28344c;
`;

const StyledInput = styled.input`
  flex: 1;
  height: 100%;
  color: black;
  padding: 10px;
  font-size: 18px;
  border: none !important;
`;

const StyledButton = styled(Button)`
  text-transform: none !important;
  font-size: 20px !important;
  background-color: #28344c !important;
  font-family: "Numans", sans-serif !important;
`;

const LoginForm = ({ onSubmit, onChange, errors, user, type, onPwChange }) => {
  if (
    localStorage.isAuthenticated &&
    JSON.parse(localStorage.isAuthenticated)
  ) {
    window.location.href = "/download";
  }

  return (
    <div className="loginBox">
      <h1>Login</h1>

      {errors.message && <p style={{ color: "red" }}>{errors.message}</p>}

      <form onSubmit={onSubmit}>
        <RowContainer>
          <Container>
            <ImageForm>
              <AlternateEmail fontSize="medium" />
            </ImageForm>
            <StyledInput
              name="email"
              placeholder="Email address"
              value={user.email}
              onChange={onChange}
              error={errors ? false : true}
              helperText={errors.email}
            />
          </Container>
          {!!errors && <span style={{ color: "red" }}>{errors.email}</span>}
        </RowContainer>

        <RowContainer>
          <Container>
            <ImageForm>
              <Key fontSize="medium" />
            </ImageForm>
            <StyledInput
              type={type}
              placeholder="Password"
              name="password"
              value={user.password}
              onChange={onPwChange}
            />
          </Container>
          {!!errors && <span style={{ color: "red" }}>{errors.password}</span>}
        </RowContainer>
        <br />
        <StyledButton
          variant="contained"
          className="loginSubmit"
          primary={true}
          size="large"
          type="submit"
        >
          Login
        </StyledButton>
      </form>

      <p className="login-text">
        You dont't have an account? <a href="/register">Register</a>
      </p>
    </div>
  );
};

export default LoginForm;
