import React from "react";
import styled from "styled-components";
import Button from "@mui/material/Button";

import "./download.css";

const StyledButton = styled(Button)`
  text-transform: none !important;
  margin-top: 20px !important;
  margin-bottom: 10px !important;
  font-size: 20px !important;
  background-color: #20a43c !important;
  font-family: "Numans", sans-serif !important;
`;

export default function Dashboard() {
  const handleDownload = () => {
    window.top.location =
      "https://drive.google.com/file/d/1Vnsb481YPq3QUY-zwa32J2kBlyxQ4CHO/view?usp=sharing";
  };

  const handleLogout = () => {
    localStorage.isAuthenticated = false;
    window.location.href = "/login";
  };

  if (
    localStorage.isAuthenticated &&
    JSON.parse(localStorage.isAuthenticated)
  ) {
  } else {
    window.location.href = "/login";
  }

  return (
    <div className="dashboardBox">
      <h1 className="dashboardBox-text">Download Unreal Kingdom</h1>

      <StyledButton
        variant="contained"
        className="downloadSubmit"
        size="large"
        onClick={handleDownload}
      >
        Download
      </StyledButton>

      <StyledButton
        variant="contained"
        className="downloadSubmit"
        size="large"
        onClick={handleLogout}
      >
        Logout
      </StyledButton>
    </div>
  );
}
