import React from "react";
import styled from "styled-components";
import { Button } from "@mui/material";
import { AlternateEmail, Key, Person } from "@mui/icons-material";
import PasswordStr from "../libraries/PasswordStr";

import "./SignUp.css";

const RowContainer = styled.div`
  margin-bottom: 20px;
  color: white;
`;

const Container = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 50px;
  margin-bottom: 2px;
`;

const ImageForm = styled.div`
  width: 50px;
  height: 100%;
  display: flex;
  align-items: center;
  padding-right: 10px;
  justify-content: center;
  background: #28344c;
`;

const StyledInput = styled.input`
  flex: 1;
  height: 100%;
  color: black;
  padding: 10px;
  font-size: 18px;
  border: none !important;
`;

const StyledButton = styled(Button)`
  text-transform: none !important;
  font-size: 20px !important;
  background-color: #28344c !important;
  font-family: "Numans", sans-serif !important;
`;

const SignUpForm = ({
  onSubmit,
  onChange,
  errors,
  user,
  score,
  type,
  pwMask,
  onPwChange,
}) => {
  if (
    localStorage.isAuthenticated &&
    JSON.parse(localStorage.isAuthenticated)
  ) {
    window.location.href = "/download";
  }

  return (
    <div className="signupBox">
      <h1>Sign Up</h1>
      {errors.message && <p style={{ color: "red" }}>{errors.message}</p>}

      <form onSubmit={onSubmit}>
        <RowContainer>
          <Container>
            <ImageForm>
              <AlternateEmail fontSize="medium" />
            </ImageForm>
            <StyledInput
              name="email"
              placeholder="Email address"
              value={user.email}
              onChange={onChange}
            />
          </Container>
          {!!errors && <span style={{ color: "red" }}>{errors.email}</span>}
        </RowContainer>

        <RowContainer>
          <Container>
            <ImageForm>
              <Person fontSize="medium" />
            </ImageForm>
            <StyledInput
              name="username"
              placeholder="Nickname"
              value={user.username}
              onChange={onChange}
            />
          </Container>
          {!!errors && <span style={{ color: "red" }}>{errors.username}</span>}
        </RowContainer>

        <RowContainer>
          <Container>
            <ImageForm>
              <Key fontSize="medium" />
            </ImageForm>
            <StyledInput
              type={type}
              placeholder="Password"
              name="password"
              value={user.password}
              onChange={onPwChange}
            />
          </Container>
          {!!errors && <span style={{ color: "red" }}>{errors.password}</span>}
        </RowContainer>

        <div className="pwStrRow">
          {score >= 1 && (
            <div>
              <PasswordStr score={score} />
              <Button
                className="pwShowHideBtn"
                onClick={pwMask}
                style={{
                  position: "relative",
                  left: "50%",
                  transform: "translateX(-50%)",
                }}
              />
            </div>
          )}
        </div>

        <RowContainer>
          <Container>
            <ImageForm>
              <Key fontSize="medium" />
            </ImageForm>
            <StyledInput
              type={type}
              name="pwconfirm"
              placeholder="Repeat password"
              value={user.pwconfirm}
              onChange={onChange}
            />
          </Container>
          {!!errors && <span style={{ color: "red" }}>{errors.pwconfirm}</span>}
        </RowContainer>
        <br />
        <StyledButton
          variant="contained"
          className="signUpSubmit"
          primary={true}
          size="large"
          type="submit"
        >
          Register
        </StyledButton>
      </form>
      <p className="signup-text">
        You have an account? <a href="/login">Login</a>
      </p>
    </div>
  );
};

export default SignUpForm;
